import {
  getWeeklyStatus
} from '../../api'
import {
  mapState,
  mapMutations
} from 'vuex'
import weeklyStatus from '../../components/weeklyStatus'

export default {
  computed: {
    ...mapState(['week', 'user']),
    pageReady() {
      return this.isReady;
    }
  },
  data() {
    return {
      isReady: false
    }
  },
  methods: {
    ...mapMutations({
      login: 'LOGIN'
    })
  },
  components: {
    weeklyStatus
  },
  onLoad() {
    uni.showLoading({
      title: "加载中"
    })
    uni.startPullDownRefresh();
  },
  onPullDownRefresh() {
    getWeeklyStatus(this.user.userId).then(res => {
      if (res[1].data.infoCode === 500) {
        uni.showToast({
          icon: 'none',
          title: res[1].data.infoText
        })
      } else {
        this.user.status = res[1].data.status;
      }
      setTimeout(function() {
        uni.stopPullDownRefresh();
      }, 500);
    })
  },
  onReady() {
    uni.hideLoading();
    this.isReady = true;
  }
}
